<template>
  <div class="tab-navigation">
    <div
      class="navigation-cat"
      v-for="(cat, index) in list"
      :key="cat.name"
      @click="setCatIndex(cat, index)"
    >
      <div class="cat-name">
        {{ cat.name }}
      </div>
      <transition name="fade">
        <div>
          <tab-navigation-item
            v-show="cat.show"
            v-for="item in cat.children"
            :key="item.name"
          >
            <div
              class="item"
              @click.stop="goto(item, item.routerIndex)"
              :class="item.routerIndex === currentIndex ? 'activeClass' : ''"
            >
              <p style="width: 60px"></p>
              <p>{{ item.name }}</p>
            </div>
          </tab-navigation-item>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import TabNavigationItem from "./TabNavigationItem.vue";

export default {
  name: "e-TabNavigation",
  props: {
    list: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      currentIndex: 1,
      currentCatIndex: 0,
    };
  },
  methods: {
    setCatIndex(cat, index) {
      cat.show = !cat.show;
    },
    goto(item, index) {
      console.log("goto index: ", index);
      this.currentIndex = index;
      window.localStorage.setItem("currentIndex010001", index);
      this.$emit("resetBasicFrameIndex");
      this.$router.replace(item.path).catch((err) => {});
    },
  },
  components: {
    TabNavigationItem,
  },
};
</script>

