.tab-navigation {
  margin-top: 50px;
}
.item {
  width: 100%;
  height: 60px;
  font-size: 20px;
  line-height: 60px;
  font-weight: lighter;

  display: flex;
  cursor: pointer;
}
.cat-name {
  padding-left: 40px;
  width: 100%;
  height: 60px;
  font-size: 25px;
  line-height: 60px;
}
.activeClass {
  background-color: var(--color-background-button-green);
}
.activeCatClass {
  background-color: #9b9b9b;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.25s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
  opacity: 0;
}
</style>
